._layoutWidget {
  background-color: $layoutWidget_bg;
  border: {
    radius: $layoutWidget_borderRadius;
    color: $layoutWidget_borderColor;
    width: $layoutWidget_borderWidth;
    style: $layoutWidget_borderStyle;
  }
  padding: {
    top: $layoutWidget_topPadding;
    right: $layoutWidget_rightPadding;
    bottom: $layoutWidget_bottomPadding;
    left: $layoutWidget_leftPadding;
  }

  + ._layoutWidget {
    margin-top: $layoutWidget_gap;
  }
}

._layoutWidget-widget {
  position: relative;
  padding: {
    top: $layoutWidget_topPadding;
    right: $layoutWidget_rightPadding;
    bottom: $layoutWidget_bottomPadding;
    left: $layoutWidget_leftPadding;
  }
}
